'use client'

import { Terminal } from '@/business/terminalLogic'
import { useEffect, useRef, useState } from 'react'

export default function Body() {
	const [input, setInput] = useState('')
	const [output, setOutput] = useState<string[]>([])
	const terminalRef = useRef(new Terminal('web terminal'))
	const bodyRef = useRef<HTMLDivElement>(null)

	const handleCommand = async (command: string) => {
		if (!command.trim()) return
		const result = await terminalRef.current.execute(command)
		setOutput(prev => [...prev, `$ ${command}`, result])
		setInput('')
	}

	const handleKeyPress = (e: React.KeyboardEvent) => {
		if (e.key === 'Enter') {
			handleCommand(input)
		}
	}

	useEffect(() => {
		if (bodyRef.current) {
			bodyRef.current.scrollTop = bodyRef.current.scrollHeight
		}
	}, [output])

	return (
		<div
			className='p-4 h-80 font-mono text-green-400 overflow-y-auto'
			ref={bodyRef}
		>
			{/* История вывода */}
			{output.map((line, index) => (
				<div key={index} className='mb-1'>
					{line}
				</div>
			))}

			{/* Компонент строки ввода */}
			<Line
				input={input}
				onInputChange={setInput}
				onKeyPress={handleKeyPress}
			/>
		</div>
	)
}
